import { Brightness7Rounded, Brightness4Rounded, WhatsApp, Send } from "@mui/icons-material";
import {
  AppBar,
  Toolbar,
  Stack,
  Box,
  MenuItem,
  Link,
  ListItemText,
  ListItemIcon,
  Select,
  IconButton,
  Button,
} from "@mui/material";
import { useState } from "react";
import { NavLink, useLocation } from "react-router-dom";
import { EnquiryDialog } from "../EnquiryDialog";
import { LanguageDictionary, RoutePathList, ActiveStyle } from "../utils";
import type { CustomAppBarProps } from "../utils";
import type { SelectProps, FabProps } from "@mui/material";

export function CustomAppBar({
  darkMode,
  setDarkMode,
  language,
  changeLanguage,
}: CustomAppBarProps): JSX.Element {
  const [openEnquiryDialog, setOpenEnquiryDialog] = useState(false);
  const { pathname } = useLocation();

  const handleLanguageChange: SelectProps<string>["onChange"] = (event) => {
    const { value } = event.target;
    changeLanguage(value);
  };

  const onRenderValue = (val: string) => {
    return <IconButton>{LanguageDictionary[val].icon}</IconButton>;
  };

  const handleToggleDarkMode = () => {
    setDarkMode((previousState) => !previousState);
  };

  const onEnquireNowClick: FabProps["onClick"] = () => {
    setOpenEnquiryDialog(true);
  };

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <Box
            component="img"
            alt="logo"
            src="https://manufac-analytics-images.s3.ap-south-1.amazonaws.com/logos/landscape/black/Manufac_Final-2.png"
            sx={{ width: "150px" }}
          />
          <Stack direction="row" spacing={2} sx={{ margin: "auto" }}>
            {Object.entries(RoutePathList).map(([name, route]) => (
              <Link
                component={NavLink}
                to={route}
                color="inherit"
                underline="hover"
                sx={route === pathname ? ActiveStyle : undefined}
                key={route}
              >
                {name}
              </Link>
            ))}
          </Stack>
          <Stack direction="row" spacing={2}>
            <Button
              variant="contained"
              color="secondary"
              size="medium"
              href="https://wa.me/+919414137952?text=Hi there, I am interested in your Guar products."
              target="_blank"
              rel="noopener noreferrer"
              startIcon={<WhatsApp />}
            >
              WhatsApp
            </Button>
            <Button
              variant="contained"
              color="secondary"
              size="medium"
              onClick={onEnquireNowClick}
              startIcon={<Send />}
            >
              Mail
            </Button>
            <Select
              value={language}
              onChange={handleLanguageChange}
              variant="outlined"
              renderValue={onRenderValue}
              sx={{ height: "40px" }}
            >
              {Object.entries(LanguageDictionary)
                .sort(([, { longForm: a }], [, { longForm: b }]) => {
                  return a.localeCompare(b);
                })
                .map(([shortForm, { icon, longForm }]) => (
                  <MenuItem value={shortForm} key={shortForm}>
                    <ListItemIcon>{icon}</ListItemIcon>
                    <ListItemText>{longForm}</ListItemText>
                  </MenuItem>
                ))}
            </Select>
            <IconButton
              title={darkMode === true ? "Switch to Light Mode" : "Switch to Dark Mode"}
              onClick={handleToggleDarkMode}
            >
              {darkMode === true ? <Brightness7Rounded /> : <Brightness4Rounded />}
            </IconButton>
          </Stack>
        </Toolbar>
      </AppBar>
      <EnquiryDialog open={openEnquiryDialog} setOpen={setOpenEnquiryDialog} />
    </>
  );
}
